<template>
  <div id="app">
    <About></About>
    <button @click="showLegume()">Legume</button>
    <button @click="showFructe()">Fructe</button>
    <Legume v-if="legume"></Legume>
    <Fructe v-if="fructe"></Fructe>
    <img v-bind:src="'./img/buttons/addtocart.jpg'" class="buy" alt="" />
  </div>
</template>

<script>
import About from './components/about.vue'
import Legume from './components/legume.vue'
import Fructe from './components/fructe.vue'

export default {
  name: 'App',
  components: {
    About,
    Legume,
    Fructe
  },
  data: function() {
    return {
      legume: false,
      fructe: false
    }
  },
  methods: {
    showLegume: function() {
      this.legume = true;
      this.fructe = false;
    },
    showFructe: function() {
      this.legume = false;
      this.fructe = true;
    }
  }
}
</script>

