<template>
  <div class="legume">
      <h2>Legume</h2>
      <div v-for="product in products" v-bind:key="product" class="product">
        <table>
          <tr>
            <td colspan="2"> <div class="title">{{product.name}}</div> </td>
          </tr>
          <tr>
            <td> <img v-bind:src="'./img/products/' + product.img" class="product-image" alt="" /> </td>
            <td>
              <div class="price">Pret: {{product.price}}</div>
              <div class="quant">Disponibil: {{product.quant}}</div>
              <div>
                <input type="number" cols="4" />
                <button class="save" @click="buyProduct(product)">Adauga</button>
              </div>
            </td>
          </tr>
        </table>
      </div>
  </div>
</template>

<script>
export default {
  name: 'legume',
  created: function() {
    this.getProducts();
  },
  data: function() {
    return {
      products: null
    }
  },
  methods: {
    buyProduct: function(product) {
      console.log(product);
      this.$http.put('https://cosul-bunicii-default-rtdb.firebaseio.com/legume/' + product.productId + '/.json',{
        productId: product.productId,
        name: product.name,
        img: product.img,
        quant: 30,
        unit: product.unit,
        price: product.price,
        buy: false
      }).then(function() {
        console.log('notifications data stored in Firebase');
        this.getProducts();
      });
    },
    getProducts: function() {
      this.$http.get('https://cosul-bunicii-default-rtdb.firebaseio.com/legume.json').then(function(data) {
        return data.json();
      }).then(function(data) {
        console.log(data);
        this.products = data;
      });
    }
  }
}
</script>

<style scoped>

  .legume {
    display: flexbox;
  }

  .product {
    margin: auto;
    border: solid 1px;
    width: 95%;
    margin-top: 10px;
  }

  .product-image {
    width: 50%;
  }

  .title, .price, .quant {
    display: block;
  }

  .title {
    font-weight: bold;
    font-size: 20px;
  }

  .buy {
    width: 50px;
  }

  table td {
    width: 300px;
  }

  input[type="number"] {
    width: 40px;
  }

  .save {
    margin-left: 10px;
  }

  table div {
    margin-top: 5px;
  }
</style>